.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    main {
        flex: 1 1 auto;
    }
}

.container-fluid {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}

// стилизация списка на index.html
.main-list {
    display: block;
    width: 100%;
    max-width: 500px;
    position: relative;
    z-index: 50;
    li {
        display: block;
        padding: 0;
        margin: 0 0 10px 0;
        a {
            font-size: 18px;
            line-height: 22px;
            color: #000;
            font-family: myriadpro;
            font-weight: 700;
            text-decoration: none;
        }
    }
}